"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";

interface LeaveRequest {
    id: number;
    student_id: number;
    reason: string;
    start_date: string;
    end_date: string;
    status: string;
    applied_at: string;
    approved_by: number | null;
}

export default function AdminLeavePage() {
    const router = useRouter();
    const [leaveRequests, setLeaveRequests] = useState<LeaveRequest[]>([]);
    const [loading, setLoading] = useState(true);
    const [filter, setFilter] = useState<string>("PENDING");

    useEffect(() => {
        fetchData();
    }, [filter]);

    const fetchData = async () => {
        try {
            const token = localStorage.getItem("token");

            if (!token) {
                router.push("/auth");
                return;
            }

            const url = filter === "ALL"
                ? "http://localhost:8000/leave/"
                : `http://localhost:8000/leave/?status=${filter}`;

            const res = await fetch(url, {
                headers: { Authorization: `Bearer ${token}` },
            });

            if (res.ok) {
                const data = await res.json();
                setLeaveRequests(data);
            }
        } catch (error) {
            console.error("Error fetching leave requests:", error);
        } finally {
            setLoading(false);
        }
    };

    const handleUpdateStatus = async (leaveId: number, status: string) => {
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`http://localhost:8000/leave/${leaveId}/status?status=${status}`, {
                method: "PUT",
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (res.ok) {
                fetchData();
            }
        } catch (error) {
            console.error("Error updating leave status:", error);
        }
    };

    const getStatusColor = (status: string) => {
        switch (status) {
            case "APPROVED":
                return "bg-green-100 text-green-800";
            case "REJECTED":
                return "bg-red-100 text-red-800";
            default:
                return "bg-yellow-100 text-yellow-800";
        }
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-100 flex items-center justify-center">
                <div className="text-xl text-gray-700">Loading leave requests...</div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-100 p-8">
            <div className="max-w-7xl mx-auto">
                <div className="mb-8">
                    <h1 className="text-4xl font-bold text-gray-800 mb-2">Leave Management</h1>
                    <p className="text-gray-600">Review and approve student leave requests</p>
                </div>

                <div className="mb-6 flex gap-3">
                    {["PENDING", "APPROVED", "REJECTED", "ALL"].map((status) => (
                        <button
                            key={status}
                            onClick={() => setFilter(status)}
                            className={`px-6 py-2 rounded-lg font-semibold transition-colors ${filter === status
                                    ? "bg-blue-600 text-white"
                                    : "bg-white text-gray-700 hover:bg-gray-100"
                                }`}
                        >
                            {status}
                        </button>
                    ))}
                </div>

                {leaveRequests.length === 0 ? (
                    <div className="bg-white rounded-xl shadow-lg p-12 text-center">
                        <div className="text-6xl mb-4">üìã</div>
                        <h2 className="text-2xl font-semibold text-gray-700 mb-2">No Leave Requests</h2>
                        <p className="text-gray-500">No {filter.toLowerCase()} leave requests found.</p>
                    </div>
                ) : (
                    <div className="grid gap-6">
                        {leaveRequests.map((leave) => {
                            const startDate = new Date(leave.start_date);
                            const endDate = new Date(leave.end_date);
                            const days = Math.ceil((endDate.getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24)) + 1;

                            return (
                                <div key={leave.id} className="bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow">
                                    <div className="flex justify-between items-start mb-4">
                                        <div className="flex-1">
                                            <div className="flex items-center gap-3 mb-2">
                                                <h3 className="text-xl font-semibold text-gray-800">
                                                    Student ID: {leave.student_id}
                                                </h3>
                                                <span className={`px-3 py-1 rounded-full text-sm font-semibold ${getStatusColor(leave.status)}`}>
                                                    {leave.status}
                                                </span>
                                            </div>
                                            <p className="text-gray-700 mb-3 text-lg">{leave.reason}</p>
                                            <div className="flex gap-6 text-sm text-gray-500">
                                                <span>üìÖ {startDate.toLocaleDateString()} - {endDate.toLocaleDateString()}</span>
                                                <span>‚è±Ô∏è {days} {days === 1 ? 'day' : 'days'}</span>
                                                <span>üïê Applied: {new Date(leave.applied_at).toLocaleDateString()}</span>
                                            </div>
                                        </div>

                                        {leave.status === "PENDING" && (
                                            <div className="flex gap-2">
                                                <button
                                                    onClick={() => handleUpdateStatus(leave.id, "APPROVED")}
                                                    className="px-6 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors font-semibold"
                                                >
                                                    ‚úì Approve
                                                </button>
                                                <button
                                                    onClick={() => handleUpdateStatus(leave.id, "REJECTED")}
                                                    className="px-6 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors font-semibold"
                                                >
                                                    ‚úó Reject
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>
        </div>
    );
}
